  ]
  created() {
      this.getFamilyList()
      this.get_room_list()
      this.get_az_list();
      this.get_status_list()
      this.get_host_attribution()
      this.getHostTypes();
      this.get_host_recycle_department()
      this.get_host_list_field()
      this.auth_list = this.$store.state.auth_info[this.$route.name];
      delete this.$store.state.host_search['']
      for(let i in this.search_option){
        this.search_option[i].default_value = this.$store.state.host_search[i]
      }
      if(this.$route.query.host_id){
        this.search_option.host_id.default_value = this.$route.query.host_id as string
      }this.fn_search();      
      
      
  }
  mounted() {
    this.setHeight()
  }
  private FnOperate(){
    this.setHeight()
  }
  setHeight(){
    this.$nextTick(()=>{
      let table = this.$refs.table as any
      this.tableHeight = window.innerHeight - table.$el.offsetTop - 70;
      let self = this;
      window.onresize = function(){
        self.tableHeight = window.innerHeight - table.$el.offsetTop - 70;
      }
    });
  }
  private async get_status_list(){
    let res:any=await Service.get_status_list({})
    if(res.code==="Success"){
      for(let i in res.data.power_status){
        this.power_list.push({text:res.data.power_status[i],value:i})
      }
      for(let i in res.data.machine_status){
        this.machine_list.push({text:res.data.machine_status[i],value:i})
      }
    }
  }
  private async get_host_list_field(){
    let res:any = await Service.get_host_list_field({
      is_op:'0'
    })
    if(res.code==="Success"){
      let key_list=['field_name','show_name'];
      let label_list=['prop','label'];
      let list:Array<any>=[]
      res.data.map(item=>{
        list=[...list,...item.filed];
        return item;
      })
      this.all_item = res.data;
      this.all_column_item = deal_list(list,label_list,key_list);
      this.get_custom_columns(this.$store.state.custom_host)

    }
  }
  private get_custom_columns(list){
    if(list.length===0){
      return;
    }    
    this.custom_host = this.all_column_item.filter(item=>list.includes(item.label));
    this.custom_host.map(item=>{
      if(['host_name','out_band_address','host_ip','cpu','ram','ecs_num','gpu_model','ram_volume','create_time','gpu_allot','gpu_count','ecs_gpu_count'].includes(item.prop)){
        item = Object.assign(item,{},{sortable:'custom'})
        if(item.prop==='ecs_num'){
          item = Object.assign(item,{},{width:'140px'})
        }
      }
      if(['cpu_with_model','net_card_with_model'].includes(item.prop)){
        item = Object.assign(item,{},{width:'180px'})
      }
      if(item.prop==='host_type_ch'){
        item = Object.assign(item,{},{column_key:'host_type',list:this.host_types})
      }
      if(item.prop==='host_purpose_ch'){
        item = Object.assign(item,{},{column_key:'host_purpose',list:this.host_uses})
      }
      if(item.prop==='host_attribution_name'){
        item = Object.assign(item,{},{column_key:'host_belong',list:this.host_belongs})
      }
      if(item.prop==='host_source'){
        item = Object.assign(item,{},{column_key:'host_source',list:this.host_source})
      }
      if(item.prop==='power_status_name'){
        item = Object.assign(item,{},{column_key:'power_status',list:this.power_list})
      }
      if(item.prop==='machine_status_name'){
        item = Object.assign(item,{},{column_key:'machine_status',list:this.machine_list})
      }
      if(item.prop==='exclusive_spec_family'){
        item = Object.assign(item,{},{column_key:'ecs_family_id',multiple:true,list:this.spec_family_list})
      }
      if(item.prop==='net_nic'){
        item = Object.assign(item,{},{width:'180px'})
      }
      if(item.prop==='vgpu_segment_type'){
        item = Object.assign(item,{},{column_key:'vgpu_segment_type',list:[{text:'Q',value:'Q'},{text:'B',value:'B'},{text:'C',value:'C'},{text:'A',value:'A'}]})
      }
      if(item.prop==='backend_type'){
        item = Object.assign(item,{},{column_key:'backend_type',list:this.backendList})
      }
      if(item.prop==='is_prepare_host'){
        item = Object.assign(item,{},{column_key:'is_prepare_host',list:this.is_pare_host})
      }
      if(item.prop==='host_business_type'){
        item = Object.assign(item,{},{column_key:'host_business_type',list:[{text:'KVM',value:'KVM'},{text:'ECI',value:'ECI'}]})
      }
      if(item.prop==='is_spot'){
        item = Object.assign(item,{},{column_key:'is_spot',list:[{text:'',value:'1'},{text:'',value:'0'}]})
      }
      return item;
    })
  }
  private fn_search(data:any={}){
    this.search_data = {...data,...this.filter_data};
    this.page_info.current = 1
    this.get_physical_list()
  }
  private beforeDestroy() {
    this.$store.commit("SET_HOST_SEARCH",this.search_data)
  }
  private FnCustom(){
    this.custom_visible=true;
  }
  private refresh(){
    this.page_info.current = 1;
    this.get_physical_list()
  }
  private async get_physical_list(){
    const {
      az_id,
      room,
      host_name,
      host_belong,
      host_purpose,
      host_type,
      out_band_address,
      host_ip,
      gpu_model,
      host_rack,
      host_source,
      ecs_family_id,
      cpu,
      nic,
      backend_type,
      product_id,
      product_name,
      power_status,
      machine_status,
      bare_metal_id,
      bare_metal_name,
      customer_keyword,
      vgpu_segment_type,
      is_prepare_host,
      host_business_type,
        is_spot,
    }=this.search_data
    let res:any=await Service.get_host_list({//
      az_id,
      machine_room_name:room,
      host_name,
      out_band_address,
      host_ip,
      gpu_model,
      host_rack,
      cpu,
      nic,
      product_id,
      product_name,
      bare_metal_id,
      bare_metal_name,
      customer_keyword,
      vgpu_segment_type: vgpu_segment_type ? vgpu_segment_type[0] : undefined,
      page_index:this.page_info.current,
      page_size:this.page_info.size,
      sort_cpu:this.search_data.sort_cpu,
      sort_ram:this.search_data.sort_ram,
      sort_ecs_num:this.search_data.sort_ecs_num,
      sort_gpu_allot:this.search_data.sort_gpu_allot,
      host_attribution_id:host_belong ? host_belong[0] : undefined,
      power_status:power_status ? power_status[0] : undefined,
      machine_status:machine_status ? machine_status[0] : undefined,
      sort_host_name:this.search_data.sort_host_name,
      sort_gpu_count:this.search_data.sort_gpu_count,
      sort_out_band_address:this.search_data.sort_out_band_address,
      sort_ecs_gpu_count:this.search_data.sort_ecs_gpu_count,
      sort_host_ip:this.search_data.sort_host_ip,
      host_purpose:host_purpose ? host_purpose[0] : undefined,
      host_type:host_type ? host_type[0] : undefined,
      host_source:host_source ? host_source[0] : undefined,
      backend_type:backend_type ? backend_type[0] : undefined,
      ecs_family_id:ecs_family_id && ecs_family_id.length>0 ? ecs_family_id.join(',') : undefined,
      is_prepare_host:is_prepare_host ? is_prepare_host[0] : undefined,
      host_business_type:host_business_type ? host_business_type[0] : undefined,
      is_spot:is_spot ? is_spot[0] : undefined
    })
    if(res.code==="Success"){
      this.list = res.data.host_list;
      this.page_info.total = res.data.page_info.count || 0
    }
  }
  private async getHostTypes(){
      let res:any =await Service.get_host_type({})
      if(res.code==='Success'){
        let key_list=['type','type_name','list'];
        let label_list=['value','text','list']
        this.host_types =deal_list(res.data,label_list,key_list);
        this.setList(this.host_types,'host_type_ch')
      }
      
  }
  private async getFamilyList(){
    let res:any =await ListService.get_family_data();
    if(res.code==='Success'){
      let key_list=['spec_family_id','name'];
      let label_list=['value','text']
      this.spec_family_list =deal_list(res.data.spec_family_list,label_list,key_list);
      if(this.custom_host.length>0){
        this.custom_host.map(item=>{
          if(item.column_key==='ecs_family_id' && item.list.length===0){
            this.$set(item,'list',this.spec_family_list)
          }
        })
      }
    }
  }
  private async get_host_attribution (){
    let res:any =await Service.get_host_attribution({})
    if(res.code==="Success"){
      let key_list=['host_attribution_id','name'];
      let label_list=['value','text']
      this.host_belongs =deal_list(res.data.host_attribution_list,label_list,key_list) 
      this.setList(this.host_belongs,'host_attribution_name')
    }
  }
  private goEcs(id){
