  private setList(list,prop){
    this.custom_host.map(item=>{
      if(item.prop===prop){
        item.list = list
      }
      return item;
    })
  }
  private close(val){
    this.oper_type==="upload" && this.get_room_list()
    val==='1' && this.get_physical_list()
    this.visible=false;
    this.oper_type='';
    this.oper_label=""
    this.multi_rows=[]
    const table =this.$refs.table as Table
    table.clearSelection()
